@import url('https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;500&display=swap');

* {
  font-family: 'DM Mono', monospace;
}

header {
  z-index: 2;
  width: 100%;
  background-color: #36415f;
  position: relative;
}

h2 {
  text-align: center;
  font-size: 28px;
  color: #374b82;
}

.topNav {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.title.container {
  display: flex;
  align-items: center;
  justify-content: start;
  box-sizing: border-box;
  margin: auto;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.navbar-nav {
  list-style: none;
  display: flex;
}

li.nav-item {
  margin-right: 25px;
}

nav a {
  color: #fff;
  text-decoration: none;
}

footer {
  background-color: #232f48;
  color: #939bad;
  padding: 40px;
  text-align: center;
}

footer .footerLogo {
  mix-blend-mode: soft-light;
}