@import url(https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'DM Mono', monospace;
}

header {
  z-index: 2;
  width: 100%;
  background-color: #36415f;
  position: relative;
}

h2 {
  text-align: center;
  font-size: 28px;
  color: #374b82;
}

.topNav {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.title.container {
  display: flex;
  align-items: center;
  justify-content: start;
  box-sizing: border-box;
  margin: auto;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.navbar-nav {
  list-style: none;
  display: flex;
}

li.nav-item {
  margin-right: 25px;
}

nav a {
  color: #fff;
  text-decoration: none;
}

footer {
  background-color: #232f48;
  color: #939bad;
  padding: 40px;
  text-align: center;
}

footer .footerLogo {
  mix-blend-mode: soft-light;
}
.wrapper {
    width: 100%;
    height: 75vh;
    min-height: 500px;
    background: #37415F;
    background: linear-gradient(0deg, #243048 0%, #37415F 100%);
}

section.title {
    width: 100%;
    height: 75vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
}

.wrapper h1,
.wrapper h2 {
    color: #d2d8db;
    z-index: 1;
    text-align: center;
}

.wrapper h1 {
    font-size: 38px;
    margin-bottom: 0;
}

.wrapper h2 {
    margin-bottom: 30px;
    font-weight: 300;
}


.wrapper h3 {
    font-size: 3rem;
}

.wrapper p {
    margin: 0;
}

img.profilePic {
    z-index: 2;
    max-width: 250px;
}

#scrollDown {
    position: absolute;
    bottom: 15%;
    left: 50%;
    width: 50px;
    margin-left: -25px;
    animation: scroll 2s infinite normal;
    z-index: 1;
}

.codeBG {
    position: absolute;
    overflow: hidden;
    top: 0;
    width: 100%;
    height: 80vh;
}

.codeBG div {
    position: absolute;
    background-color: transparent;
    color: #434d67;
    font-size: 6rem;
    font-family: consolas;
    width: 100%;
}

.codeBG div:nth-child(1) {
    top: 12%;
    left: 42%;
    animation: bg 10s linear infinite;
}

.codeBG div:nth-child(2) {
    top: 70%;
    left: 50%;
    animation: bg 7s linear infinite;
}

.codeBG div:nth-child(3) {
    top: 17%;
    left: 6%;
    animation: bg 9s linear infinite;
}

.codeBG div:nth-child(4) {
    top: 46%;
    left: 60%;
    animation: bg 10s linear infinite;
}

.codeBG div:nth-child(5) {
    top: 60%;
    left: 10%;
    animation: bg 6s linear infinite;
}

.codeBG div:nth-child(6) {
    top: 80%;
    left: 70%;
    animation: bg 12s linear infinite;
}

.codeBG div:nth-child(7) {
    top: 60%;
    left: 80%;
    animation: bg 15s linear infinite;
}

.codeBG div:nth-child(8) {
    top: 32%;
    left: 25%;
    animation: bg 16s linear infinite;
}

.codeBG div:nth-child(9) {
    top: 90%;
    left: 25%;
    animation: bg 9s linear infinite;
}

.codeBG div:nth-child(10) {
    top: 20%;
    left: 80%;
    animation: bg 5s linear infinite;
}

@media (max-width: 750px) {
    section.title {
        padding: 20px;
    }

    .wrapper h1 {
        font-size: 34px;
    }

    .wrapper h2 {
        font-size: 20px;
    }

    img.profilePic {
        max-width: 200px;
    }
}

/* Animations */

@keyframes bg {
    0% {
        transform: scale(0) translate(0, 0);
        opacity: 1;
    }

    100% {
        transform: scale(1.3) translate(-400px, -50px);
        opacity: 0;
    }
}

@keyframes scroll {
    0% {
        transform: translateY(-20px);
        opacity: 0;
    }

    50% {
        transform: translateY(0px);
        opacity: 0.8;
    }

    80% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}
#aboutMe {
    max-width: 600px;
}

#aboutMe p {
    color: #2b3752;
    font-size: 17px;
    line-height: 1.7;
}

#aboutMe a.button {
    border: 1px solid #374b82;
    display: block;
    width: 150px;
    text-align: center;
    padding: 10px;
    margin: auto;
    border-radius: 28px;
    text-decoration: none;
    color: #374b82;
    margin-top: 40px;
}

#aboutMe a.button:hover {
    background-color: #253147;
    color: #fff;
}

hr {
    margin: 45px 0 0;
}
.skillIcon img {
    max-width: 200px;
    max-height: 100px;
    margin: 20px;
}

.skills {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
